import fetch from "isomorphic-fetch";

export const REQUEST_DATA = 'REQUEST_DATA';
export const RECEIVE_DATA = 'RECEIVE_DATA';

export const TOPICS_DATA = 'TOPICS_DATA';//

export const DETAIL_DATA = 'DETAIL_DATA';//
export const GET_DETAIL_ISFETCH = 'GET_DETAIL_ISFETCH';//
export const DETAIL_ISSUC = 'DETAIL_ISSUC';//
export const DETAIL_UPS_INFO = 'DETAIL_UPS_INFO';//
export const DETAIL_REPLIES_INFO = 'DETAIL_REPLIES_INFO';//


export const MESSAGE_COUNT = 'MESSAGE_COUNT';//
export const MESSAGE_DATA = 'MESSAGE_DATA';//

export const CREACT_TOPIC = 'CREACT_TOPIC';//
export const CREACT_TOPIC_ISSUC = 'CREACT_TOPIC_ISSUC';//

export const CHECK_USER = 'CHECK_USER';//
export const USER_DATA = 'USER_DATA';//
export const SAVE_USER_KEY = 'SAVE_USER_KEY';//accessToken

export const TAB_BAR = 'TAB_BAR';//homefoot
export const SELECT_TAB = 'SELECT_TAB';//homefoot
export const TAB_INDEX = 'TAB_INDEX';//

export const TOPIC_COLLECT ='TOPIC_COLLECT';//
export const TOPIC_DECOLLECT ='TOPIC_DECOLLECT';//
export const COLLECT_DATA ='COLLECT_DATA';//


/* 
  
*/
export const requestData = (isfetch) => {
  return {
    type: REQUEST_DATA,
    isfetch,
  }
}
export const receiveData = (isfetch) => {
  return {
    type: RECEIVE_DATA,
    isfetch,
  }
}
/* 
 get 
page Number 
tab String  ask share job good
limit Number 
mdrender String  false  true markdown 
https://cnodejs.org/api/v1/topics
*/
export const topicsData = (tab,page,data,askData,shareData,) => {
  return {
    type: TOPICS_DATA,
    data,
    askData,
    shareData,
    tab,
    page,
  }
}
export const getTopics = (tab,page=1,limit=20) => {
  return dispatch => {
    fetch(`https://cnodejs.org/api/v1/topics?tab=${tab}&page=${page}&limit=${limit}`)
    .then(response => response.json())
    .then(json => {dispatch(topicsData(tab,page,json.data));})
  }
}

// 
/* 
 get 
mdrender String  false  true markdown 
accesstoken String  is_collect  replies  is_uped  
https://cnodejs.org/api/v1/topic/5433d5e4e737cbe96dcef312
*/
export const detailData = (data) => {
  return {
    type: DETAIL_DATA,
    data
  }
}
export const getDetailIsfetch = (fetchState) => {
  return {
    type: GET_DETAIL_ISFETCH,
    fetchState
  }
}

export const detailIsSuc = (IsSucBool) => {
  return {
    type: DETAIL_ISSUC,
    IsSucBool
  }
}

export const getDetail = (id,accesstoken=null) => {
  return dispatch => {
    dispatch(getDetailIsfetch(true))
    fetch(`https://cnodejs.org/api/v1/topic/${id}?accesstoken=${accesstoken}`)
    .then(response => response.json())
    .then(json => {
      dispatch(detailData(json));
      if(json.success){
        dispatch(detailIsSuc(json.success));
      }
      dispatch(getDetailIsfetch(false))
      
    })
  }
}

/* 
post /reply/:reply_id/ups 
 post 
accesstoken String
 action up or down


{"success": true, "action": "down"}
*/
export const detailUpsInfo = (upsInfo) => {
  return {
    type: DETAIL_UPS_INFO,
    upsInfo,
  }
}

export const postDetailUps = (id,key) => {
  return dispatch => {
    fetch(`https://cnodejs.org/api/v1/reply/${id}/ups `,{
      method:"POST",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded"
      },
      body:`accesstoken=${key}`
    })
    .then(response => response.json())
    .then(json => {
      dispatch(detailUpsInfo(json));
    })
  }
}

/* 
post /topic/:topic_id/replies 
 post 
accesstoken String  accessToken
content String 
reply_id String 

{success: true, reply_id: '5433d5e4e737cbe96dcef312'}
*/

export const postDetailReply = (topic_id,accesstoken,content,reply_id=null) => {
  let reply = '';
  reply_id===null ? reply='' : reply=`&reply_id=${reply_id}`;
  return dispatch => {
    fetch(`https://cnodejs.org/api/v1/topic/${topic_id}/replies`,{
      method:"POST",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded"
      },
      body:`accesstoken=${accesstoken}&content=${content}${reply}`
    })
    .then(response => response.json())
    .then(json => {dispatch(detailRepliesInfo(json));})
  }
}
export const detailRepliesInfo = (replyInfo) => {
  return {
    type: DETAIL_REPLIES_INFO,
    replyInfo,
  }
}

